.bottom-area {
  display: flex;

  @media screen and (max-width: 767px) {
    flex-direction: column;
    align-items: center;
  }

  .news {
    flex: 0 0 35%;
    max-height: 250px;
    overflow-y: scroll;
    
    @media screen and (max-width: 767px) {
      order: 2;
    }

    .single-news {
      margin: 0 10px;
      padding: 10px 0;

      &:not(:last-child) {
        border-bottom: 1px solid lightgrey;
      }

      a {
        text-decoration: none;
        color: black;

        &:hover {
          color: #bd7700;
        }
      }

      .published-time {
        color: grey;
        margin-bottom: 10px;
        font-size: 12px;
    }
    }
  }

  .forecast-weather {
    flex: 0 0 65%;
    display: flex;
    justify-content: space-around;

    @media screen and (max-width: 767px) {
      order: 1;
    }

    .forecast-single {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;

      .forecast-weather--sky {
        max-width: 80px;
      }
    }
  }
}